import React from 'react'
import { Button } from 'reactstrap'

import styles from './MyTableForm.module.scss'

const MyTableForm = ({
  className, onSubmit, children, isValid, buttonText='', resetForm, ...props
}) => {

    const cls = [
        styles.MyTableForm,
        className
    ]

    const onReset = resetForm || (() => console.log('  '))



    return (
        <div className={cls.join(' ')}>
           <form onSubmit={onSubmit}>
                <div>
                   {children}
                </div>


               <div className={styles.buttonsWrap} >
                <Button
                        color={isValid? "primary": 'secondary'}
                        disabled={!isValid}
                        className={styles.button}
                    >
                    
                        {buttonText}
                    </Button>

                    { resetForm && (
                        <Button
                            type='reset'
                            className={styles.button}
                            onClick={onReset}
                        >
                            
                        </Button>
                    )}
               </div>

            </form>
        </div>
    )
}

export default MyTableForm