import { SET_SHOPS, SET_SHOP_DETAILS, SHOPS_FAILURE, SHOPS_LOADING, SHOPS_SUCCESS } from "../actions/actionTypes";


const initialState = {
  shops: [],
  loading: false,
  details: {
    uuid: '',
    partner_uuid: '',
    name: '',
  },
  errors: {
    shops: null,
    details: null,
    update: null,
    delete: null,
    create: null,
  },
  success: {
    update: null,
    delete: null,
    create: null,
  },
  filter: {
    limit: 100,
    name: '',
    offset: 0,
    sort_by: [],
    uuid: '',
    partner_uuid: '',
  }
}

export default function shopReducer(state = initialState, action) {
  switch (action.type) {
    case SHOPS_LOADING:
      return {
        ...state,
        loading: action.status,
      };
    case SHOPS_SUCCESS:
      return {
        ...state,
        success: {
          ...state.success,
          ...action.message,
        },
      };
    case SHOPS_FAILURE:
      return {
        ...state,
        errors: {
          ...state.errors,
          ...action.errors,
        },
      };
    case SET_SHOP_DETAILS:
      return {
        ...state,
        details: action.details,
      };
    case SET_SHOPS:
      return {
        ...state,
        shops: action.shops,
      };

    default:
      return state;
  }
}
