import { SET_TERMINALS, SET_TERMINAL_DETAILS, TERMINALS_FAILURE, TERMINALS_LOADING, TERMINALS_SUCCESS } from "../actions/actionTypes";


const initialState = {
  terminals: [],
  loading: false,
  details: {
    uuid: '',
    acquirer_uuid: '',
    name: '',
    config: {},
    currency_num: '',
    config_id: '',
    operations: [],
    store_uuid: ''
  },
  errors: {
    terminals: null,
    details: null,
    update: null,
    delete: null,
    create: null,
  },
  success: {
    update: null,
    delete: null,
    create: null,
  },
  filter: {
    limit: 100,
    name: '',
    offset: 0,
    sort_by: [],
    uuid: '',
  }
}

export default function terminalReducer(state = initialState, action) {
  switch (action.type) {
    case TERMINALS_LOADING:
      return {
        ...state,
        loading: action.status,
      };
    case TERMINALS_SUCCESS:
      return {
        ...state,
        success: {
          ...state.success,
          ...action.message,
        },
      };
    case TERMINALS_FAILURE:
      return {
        ...state,
        errors: {
          ...state.errors,
          ...action.errors,
        },
      };
    case SET_TERMINAL_DETAILS:
      return {
        ...state,
        details: action.details,
      };
    case SET_TERMINALS:
      return {
        ...state,
        terminals: action.terminals,
      };

    default:
      return state;
  }
}
