import { ACCOUNTS_LOADING, SET_ACCOUNTS_DETAILS, ACCOUNTS_FAILURE, SET_ACCOUNTS, ACCOUNTS_SUCCESS } from "../actions/actionTypes";

const initialState = {
  accounts: [],
  loading: false,
  details: {
    uuid: '',
  },
  errors: {
    accounts: null,
    details: null,
    update: null,
    delete: null,
    create: null,
  },
  success: {
    update: null,
    delete: null,
    create: null,
  },
  filter: {
    limit: 100,
    name: '',
    offset: 0,
    sort_by: [],
    uuid: ''
  }
}

export default function accountReducer(state = initialState, action) {
  switch (action.type) {
    case ACCOUNTS_LOADING:
      return {
        ...state,
        loading: action.status
      }

    case SET_ACCOUNTS:
      return {
        ...state,
        accounts: action.accounts
      }

    case SET_ACCOUNTS_DETAILS:
      return {
        ...state,
        details: action.details
      }

    case ACCOUNTS_FAILURE:
      return {
        ...state,
        errors: {
          ...state.errors,
          ...action.errors
        }
      }

    case ACCOUNTS_SUCCESS:
      return {
        ...state,
        success: {
          ...state.success,
          ...action.message
        }
      }

    default:
      return state;
  }
}